import { newSpecPage } from '@stencil/core/testing';
import { PrestoYoutubeSubscribeButton } from '../presto-youtube-subscribe-button';

describe('presto-youtube-subscribe-button', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoYoutubeSubscribeButton],
      html: `<presto-youtube-subscribe-button></presto-youtube-subscribe-button>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-youtube-subscribe-button>
        <div class="g-ytsubscribe"></div>
      </presto-youtube-subscribe-button>
    `);
  });
});
